*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;

}
h1{
    text-align: center;
    font-family: 'Jost', sans-serif;
}

.box{
    width: 35%;
    height: 350px;
 /* background-color: #c795a8;*/
    margin: 5% auto;
  /*  background-image: linear-gradient(#00b78a,#6701ff);*/
    background-image: url(wave.svg);
    background-size: cover;
    padding: 10px;
    border-radius: 20px;
    display: grid;
    font-family: 'Jost', sans-serif;
    box-shadow: rgb(209, 40, 156) 3px 3px 6px 0px ;
}

.box:hover{
    cursor: pointer;
    background-color: #c290a4;
}

.inputFeild{ 

    display: grid;
    
    
}

i{
    font-size: 50px;
    padding: 20px;
    text-align: center;
    
}
.errormsg{
    text-align: center;
    font-size: 20px;
}

h2{
    padding: 5px;
    text-align: center;
}

h2:hover{
    color: #8c381e;
    cursor: pointer;
}

.inputfeilds{
    text-align: center;
    padding: 8px;
    width: 35%;
    height: 30px;
    margin: 20px auto;
    /*border: 0.3px solid #984063;*/
    background-color: #FE9677;
    border-radius: 20px;
    font-size: 16px;
    border: none;
    font-family: 'Jost', sans-serif;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
    
}
body{
    background-color: #41436A;
  /*  background-image: url(Hexagon.svg);*/
}

.info{
    text-align: center;
    background: none;
}
.info h1{
    padding: 5px;
    background: none;
}

.info h2{
    padding: 5px;
    background: none;
}

.info h3{
    padding: 5px;
    background: none;
}

@media only screen and (max-width: 600px) {
   body {
      background-color: lightblue;
    }

    .box{
        width: auto;
        margin: 10px;
    }
  }

